.MainPage{
    display: flex;
    flex-direction: column;
}
.trovaIlTuoCucciolo{
    height: 450px;
    background-image: url('https://www.plateauveterinary.net/static/sitefiles/pages/puppy-banner.jpg');
    background-position: 0px 0px;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    position: relative;
}
.barraRicercaWrap{
    width: 700px;
    height: 55px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    border-radius: 20px;
    background-color: white;
    display: flex;
    box-shadow: rgba(77, 71, 81, 0.2) 0px 1px 6px 0px;
}
.barraRicerca{
    width: calc(100% - 55px);
    height: 100%;
    border-radius: 20px;
    border: none;
    font-size: 16px;
    text-indent: 10px;
}
.barraRicerca:focus-visible{
    outline: none;
}
.bottoneRicerca{
    width: 35px;
    height: 35px;
    border-radius: 10px;
    margin: auto;
    border: none;
    background-color: var(--coloreSpeciale);
    display: flex;
}
.lente{
    color: white;
    margin: auto;
}
.frasiPrincipali{
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.frasiPrincipali *{
    width: fit-content;
    color: white;
}
.frasePrincipale1{
    font-size: 45px;
}
.frasePrincipale2{
    font-size: 19px;
}
.caniEgattiWrap{
    display: flex;
    flex-direction: column;
    position: relative;
    margin: auto;
    top: -100px;
    border-radius: 40px;
    box-shadow: rgba(77, 71, 81, 0.2) 0px 1px 6px 0px;
    background-color: white;
    padding: 40px;
}
.divSelezione{
    display: flex;
    flex-direction: row;
    gap: 40px;
    height: 322px;
    width: 1040px;
}
.divSelezioneAperto{
    display: flex;
    flex-direction: row;
    height: 322px;
    width: 1040px;
}
.scrittaCanigatti{
    font-size: 30px;
    text-align: center;
    margin-bottom: 30px;
    font-weight: 500;
}
.animaliD{
    height: 320px;
    width: 320px;
    border-radius: 20px;
    font-size: 48px;
    display: flex;
    color: white;
    overflow: hidden;
    position: relative;
    box-sizing: content-box;
    border-bottom: 2px solid ;
    transition: .2s;
    cursor: pointer;
}
.animaliDaperto{
    width: 100%;
    height: 100%;
    border-radius: 20px;
    box-shadow: rgba(77, 71, 81, 0.2) 0px 1px 6px 0px;
}
.animaliD:hover{
    border-bottom: 2px solid var(--coloreSpeciale);
}   
.cani{    
    
    box-shadow: rgba(77, 71, 81, 0.2) 0px 1px 6px 0px;

}
.divScuro{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: .2s;
    cursor: pointer;
    position: absolute;
    left: -100%;
    display: flex;
    
}
.scrittaCards{
    width: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    z-index: 1000;
    display: flex;
    padding: 20px;
    text-align: end;
    display: flex;
    align-items: flex-end;
    line-height: 72px;
    box-shadow: rgba(77, 71, 81, 0.2) 0px 1px 6px 0px;
}
.cani,#scrittaCardsCane{
    background-image: url('https://images.unsplash.com/photo-1591946559594-8c6d3b7391eb?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80 ');
    background-position: 0px -140px;
    background-repeat: no-repeat;
    background-size: cover;

    background-image: url('https://images.pexels.com/photos/220938/pexels-photo-220938.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1');
    background-position: 0px 0px;
}
.gatti,#scrittaCardsGatto{
    background-image: url('https://images.unsplash.com/photo-1583795128727-6ec3642408f8?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1057&q=80');
    background-position: 0px 0px;
    background-repeat: no-repeat;
    background-size: cover;
}
.altro,#scrittaCardsAnim{
    background-image: url('https://images-ext-1.discordapp.net/external/YhMVflVuj0vX8_oZgm6z1ZTmvKJPEpxDrsC_6jOquJA/%3Fixlib%3Drb-4.0.3%26ixid%3DMnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8%26auto%3Dformat%26fit%3Dcrop%26w%3D687%26q%3D80/https/images.unsplash.com/photo-1555169062-013468b47731?width=454&height=681');
    background-position: 0px -100px;
    background-repeat: no-repeat;
    background-size: cover;
}

