.divSelAperto{
    border: 1px solid rgba(77, 71, 81, 1);
    width: 100%;
    border-radius: 20px;
    box-shadow: rgba(77, 71, 81, 0.2) 0px 1px 6px 0px;
    display: flex;
    flex-direction: column;
}
.parteSuperioreDivSel{
    width: 100%;
    height: 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    font-size: 20px;
    position: relative;
}
.parteSuperioreDivSel::after{
    content: '';
    position: absolute;
    width: 100%;
    border-bottom: 1px solid black;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0px;
    border-radius: 20px;
}
.iconsDivSel, .frecciaSx,.frecciaDx{
    font-size: 35px;
    border-radius: 10px;
    cursor: pointer;    
}
.iconsDivSel:hover, .frecciaSx:hover,.frecciaDx:hover{
    background-color: var(--coloreSpeciale);
    color: white;
}



.parteInferiore{
    height: 100%;
    padding: 20px;
    display: flex;
    flex-direction: row;
}
.parteInferioreDX{
    height: 100%;
    width: calc(80% - 20px);
    margin-left: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    border-radius: 10px;
}
.cardSelezioneIDwrapper{
    display: flex;
    flex-direction: row;
    width: 100%;
    flex-wrap:wrap;
    row-gap: 0px;
    gap: 10px;
    row-gap: 0;
    align-content: flex-start;
    row-gap: 10px;
    position: relative;
    z-index: 1;
}
.cardSelezioneIDwrapper::before{
    content: '';
    position: absolute;
    height: 80%;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    border-left: 1px solid rgba(77, 71, 81, 0.2);
    left: -20px;
    z-index: 0;
}
.cardID{
    height: calc(25% - 5px);
    aspect-ratio: 3/1;
    border: 2px solid var(--coloreSpeciale);
    color: var(--coloreSpeciale);
    border-radius: 30px;
    background-color: rgb(255, 255, 255);
    padding: 10px;
    display: flex;
    flex-direction: column;
    font-size: 18px;
    cursor: pointer;
    z-index: 1;

}
.cardIDinList{
    height: calc(25% - 5px);
    aspect-ratio: 3/1;
    border: 2px solid var(--coloreSpeciale);
    color: white;
    border-radius: 30px;
    background-color: var(--coloreSpeciale);
    padding: 10px;
    display: flex;
    flex-direction: column;
    font-size: 18px;
    cursor: pointer;
    z-index: 1;
}
.cardID:hover{
    background-color: rgb(230, 230, 230);
}

.titoloCardID{

}
.contenutoCardID{
    font-weight: 500;
    margin: auto;
}
.identitaAnimale{
    width: 20%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid black;

}
.titoloIdentita{
    justify-self: center;
    align-self: center;
    font-size: 25px;
    font-weight: 500;
}
.elIdentita{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: 300;
    text-transform: capitalize;
}
.taglia{
}
.divSelezioneId{
    width: 300px;
    height: 55px;
    border: 1px solid rgba(77, 71, 81, 0.2);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 20px;
}
.divSelezioneId:hover{
    background-color:rgba(77, 71, 81, 0.2);
}
.divSelezioneId .titolo{
    font-size: 15px;
}
.divSelezioneId .sottoTitolo{
    font-size: 12px;
    color: rgba(77, 71, 81, 0.5);
    font-weight: 400;
}
.divSelId{
    border: 1px solid rgba(77, 71, 81, 0.2);
    width: 300px;
    position: absolute;
    margin-top: 60px;
    border-radius: 20px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    gap: 10px;
}
.opzioneId{
    height: 35px;
    border: 1px solid rgba(77, 71, 81, 0.2);
    width: 100%;
    border-radius: 10px;
    font-size: 15px;
    font-weight: 400;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 300;
    background-color: white;
    cursor: pointer;
}
.opzioneId:hover{
    background-color:rgba(77, 71, 81, 0.2);
}
.opzioneId:first-child{
    font-weight: 500;
    margin-bottom: 10px;
}

.etaWrap{
    display: flex;
    flex-direction: column;
}
.divSelIdEta{
    height: 55px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.eta{
    font-weight: 400;
}
#eta0{
    opacity: 0.3;
}
#eta1{
    font-size: 20px;
    opacity: 0.5;
}
#eta2{
    font-size: 25px;
}
#eta3{
    font-size: 20px;
    opacity: 0.5;
}
#eta4{
    opacity: 0.3;
}
.frecciaSx{
    margin-right: auto;
    font-size: 35px;
}
.frecciaDx{
    margin-left: auto;
    font-size: 35px;
}
.bottoneEta{
    position: absolute;
    margin-top: 160px;
    width: 300px;
    height: 55px;
    border-radius: 20px;
    border: 1px solid rgba(77, 71, 81, 0.2);
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
}
.bottoneEta:hover{
    background-color: var(--coloreSpeciale);
    color: white;
}
.elementoId{
    background-color: var(--coloreSpeciale);
    color: white;
    padding: 6px;
    padding-top: 3px;
    padding-bottom: 3px;
    border-radius: 6px;
    display: flex;
    width: fit-content;
    font-weight: 400;
    font-size: 15px;
    line-height: 15px;
    box-shadow: rgba(77, 71, 81, 0.2) 0px 1px 6px 0px;
    cursor: pointer;
}
.xelementoId{
    font-weight: 400;
    text-align: center;
    font-size: 15px;
    margin-top: auto;
    margin-bottom: auto;
    line-height: 15px;
    margin-left: 5px;
    
}
.elementiIdWrap{
    margin-left: 10px;
    display: flex;
    flex-direction: row;
    gap: 5px;
    min-width: 270px;
    overflow-x: scroll;
    position: relative;
}
.elementiIdWrap::-webkit-scrollbar {
    display: none;
}
